.header {
  background: rgb(18, 18, 18, 0);
  /* box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1); */
  position: fixed;
  width: 100%;
  z-index: 3;
  /* backdrop-filter: blur(10px); */
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: #181818;
  align-items: center;
  text-align: center;
  height: 100vh;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.header li a {
  display: block;
  padding: 25px 20px;
  /* border-right: 1px solid #f4f4f4; */
  text-decoration: none;
  color: white;
  align-items: center;
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
}

